.loader {
  width: 100vw;
  height: 100vh;
  background: none;
  justify-content: center;
  align-items: center;
  transition: opacity .75s, visibility .75s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.loader--hidden {
  opacity: 0;
  visibility: hidden;
}

.loader:after {
  content: "";
  width: 75px;
  height: 75px;
  border: 15px solid var(--primary-color-btn);
  border-top-color: var(--bgc-background);
  border-radius: 50%;
  animation: loading .75s infinite;
}

@keyframes loading {
  from {
    transform: rotate(0turn);
  }

  to {
    transform: rotate(1turn);
  }
}

